.list {
  display: flex;
  flex-direction: column;
  font-size: 14px;
  padding-left: 0px;
}

.head_row {
  list-style: none;
  display: flex;
}
.container {
  width: 100%;
  min-height: 100px;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.text {
  text-align: center;
  margin: 24px auto;
}

/* MOBILE ONLY */
@media (max-width: 767.98px) {
  .list {
    margin-top: 32px;
    align-items: center;
    gap: 8px;
    padding: 25px;
  }
  .container {
    margin-top: 40px;
  }
}
/* TABLET */
@media (min-width: 768px) {
  .list {
    /* width: 704px; */
    max-height: calc(100vh - 500px);
    margin: auto;
    overflow: auto;
    scrollbar-width: thin;
    width: 90%;
    max-width: 704px;
    box-sizing: border-box;
  }
  .container {
    width: 704px;
    height: calc(100vh - 500px);
  }

  .head_row {
    width: 704px;
    margin: auto;
    padding: 16px 20px;

    display: flex;
    align-items: center;
    gap: 8px;

    text-transform: capitalize;

    border-radius: 8px;
    background: rgba(82, 59, 126, 0.6);
    box-shadow: 0px 4px 60px 0px rgba(0, 0, 0, 0.25);
    backdrop-filter: blur(50px);
    font-weight: 600;
    font-size: 14px;

    &::after {
      display: none;
    }
  }

  .row_item {
    text-wrap: wrap;
    color: var(--text-color);
    flex-grow: 0;
  }

  .row_item:first-child {
    width: 105px;
    margin-left: 0;
  }
  .row_item:nth-child(2) {
    width: 56px;
    text-align: center;
  }
  .row_item:nth-child(3) {
    width: 130px;
  }
  .row_item:nth-child(4) {
    width: 150px;
  }
  .row_item:nth-child(5) {
    width: 88px;
    text-align: center;
  }
}
/* DESKTOP */
@media (min-width: 1280px) {
  .list,
  .container {
    /* width: 715px; */
    max-height: calc(100vh - 292px);
  }
}
